<template>
	<view class="content">
		<view>
			<uni-icons type="weixin" size="120" color="#04BE02"></uni-icons>
		</view>
		<view class="text-area">
			<button class="bottom" type="primary" @tap="wxLogin">授权登录</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				title: '微信授权登录',
				host:'' //配置为自己的额服务器即可
			}
		},
		onLoad() {

		},
		methods: {
			wxLogin() {
				// 获取code 小程序专有，用户登录凭证。
				uni.getUserProfile({
					desc: "获取用户基本资料",
					lang: 'zh_CN',
					success: (user) => {
						//获取成功基本资料后开启登录，因为基本资料首先要授权
						uni.login({
							provider: 'weixin',
							success: function(code_res) {
								if (code_res.errMsg == "login:ok") {
									let code = code_res.code;
									console.log(user.userInfo);
									//console.log(code);
									uni.request({
									    url: 'https://api.weixin.qq.com/sns/jscode2session',
									    data: {
									        "appid":"wxbbc48eccdfa1006d",
											"secret":"69ae0e82867cf6ca4c095a272c43282b",
											"js_code":code,
											"grant_type":"authorization_code"
									    },
									    success: (res) => {
									        //console.log(res.data);
											let wxuid = res.data.openid;
											console.log(wxuid);
									    }
									});
								}
							}
						});
			
					},
					fail: (res) => {
						uni.showModal({
							title:"用户拒绝授权",
							showCancel:false
						})
					}
				});
			},
		}
	}
</script>

<style>
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}
	.text-area {
		display: flex;
		justify-content: center;
	}

	.title {
		font-size: 36rpx;
		color: #8f8f94;
	}
</style>
